.ldsDualDing {
    display: inline-block;
    position: absolute;
    top: 45%;
    left:50%;
    transform:translate(-50%, -50%);
    width: 80px;
    height: 80px;
}

.ldsDualDing::after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #000;
    border-color: #000 transparent #000 transparent;
    animation: ldsDualDing 1.2s linear infinite;
}
        
    
  

@keyframes ldsDualDing {
    0%{ 
      transform: rotate(0deg)
    }
    100% {
      transform: rotate(360deg)
    }
}